<template>
  <div class="mission">
    <el-table
      :data="missions.data"
      border
      style="width: 100%">
      <el-table-column
        property="id"
        label="id"
        width="180">
      </el-table-column>
      <el-table-column
        property="account"
        label="账号"
        width="180">
      </el-table-column>
      <el-table-column
        property="status"
        label="状态"
        width="180">
      </el-table-column>
      <el-table-column
        inline-template
        label="操作">
        <el-button type="primary" size="small" @click.native="cancel(row.id)">操作</el-button>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import MissionService from '../services/mission';
  
  export default {
    data() {
      return {
        missions: {
          totalCount: 200,
          data: [
            {
              id: 1,
              account: 'xx@xx.com',
              status: 0,
            },
            {
              id: 2,
              account: 'xx@xx.com',
              status: 0,
            }
          ]
        },
        currentPage: 1,
        limit: 10,
        totalCount: 0,
      };
    },
    computed: {
      totalCount() {
        return this.missions.totalCount;
      }
    },
    created() {
      this.currentPage = +this.$route.query.page || 1;
      this.getList();
    },
    mounted() {
//      window.setInterval(() => this.getList(), 3000);
    },
    methods: {
      async getList(page = this.currentPage, ...param) {
        const params = {
          page,
          limit: this.limit,
          ...param,
        };
        try {
          const { data } = await MissionService.getList(params);
          this.missions = data.data || {};
        } catch (e) {
          console.log(e.data);
        }
        this.$router.push({
          query: {
            page: this.currentPage,
          }
        });
      },
      
      async cancel(id) {
        console.log(id);
        try {
          await MissionService.cancel(id);
        } catch (e) {
          console.log(e.data);
        }
      },
      handleSizeChange(size) {
        this.limit = size;
        this.getList();
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage;
        this.getList();
      },
    },
  };
</script>
<style scoped>
  .pagination {
    text-align: right;
    border-right: 1px solid transparent;
    border-left: 1px solid transparent;
  }
</style>



