import Vue from 'vue';
import App from './app';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import Element from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import router from './routes';

Vue.use(Element);
Vue.use(VueRouter);
Vue.use(VueResource);
new Vue({ // eslint-disable-line
  el: '#app',
  render: h=>h(App),
  router
});




